
import { defineComponent, onMounted, ref, watch } from "vue";
import { CanvasRenderer } from "echarts/renderers";
import VChart from "vue-echarts";
import * as echarts from "echarts/core";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  LegendComponentOption,
} from "echarts/components";
import { PieChart } from "echarts/charts";
import { LabelLayout } from "echarts/features";
import * as d3 from "d3";
import { number } from "echarts/core";
import { statusStore } from "@/store/index";

echarts.use([
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  PieChart,
  CanvasRenderer,
  LabelLayout,
]);

export default defineComponent({
  components: { VChart },
  props: ["type", "title", "data"],
  setup(props, { attrs }: any) {
    const status = statusStore();
    const tabs_switch = ref();
    const chartDOM = ref();
    const main = ref();
    let only = ref<boolean>(false);
    let legendData: any = [];
    let Tooltip: any;

    for (let item of props.data) {
      legendData.push({ name: item.label, value: item.value });
    }

    if (props.type === "3" && props.title === "") {
      only.value = true;
    }

    onMounted(() => {
      Tooltip = d3
        .select(".container_chart")
        .append("div")
        .style("z-index", 10)
        .style("opacity", 0)
        .attr("class", "tooltips");

      let pie = echarts.init(chartDOM.value);
      switch (props.type) {
        case "1":
          pie.setOption(legendOption);
          break;
        case "2":
          pie.setOption(noLegendOption);
          break;
        case "3":
          pie.setOption(noLegendOption);
          break;

        default:
          break;
      }
      window.addEventListener("resize", () => {
        pie.resize();
      });

      if (status.mode === "dark" && props.type === "1") {
        pie.setOption(legendDarkOption, true);
      }

      watch(
        () => status.mode,
        (newMode) => {
          if (newMode === "dark") {
            switch (props.type) {
              case "1":
                pie.setOption(legendDarkOption);
                break;
              case "2":
                pie.setOption(noLegendOption);
                break;
              case "3":
                pie.setOption(noLegendOption);
                break;

              default:
                break;
            }
          } else {
            switch (props.type) {
              case "1":
                pie.setOption(legendOption);
                break;
              case "2":
                pie.setOption(noLegendOption);
                break;
              case "3":
                pie.setOption(noLegendOption);
                break;

              default:
                break;
            }
          }
        },
        { deep: true }
      );

      watch(
        () => props.data,
        (newData, oldData) => {
          if (newData != oldData) {
            legendData = [];
            for (let item of newData) {
              legendData.push({ name: item.label, value: item.value });
            }
            legendDarkOption.series[0].data = legendData;
            noLegendOption.series[0].data = legendData;
            legendOption.series[0].data = legendData;

            if (status.mode === "dark") {
              switch (props.type) {
                case "1":
                  pie.setOption(legendDarkOption);
                  break;
                case "2":
                  pie.setOption(noLegendOption);
                  break;
                case "3":
                  pie.setOption(noLegendOption);
                  break;

                default:
                  break;
              }
            } else {
              switch (props.type) {
                case "1":
                  pie.setOption(legendOption);
                  break;
                case "2":
                  pie.setOption(noLegendOption);
                  break;
                case "3":
                  pie.setOption(noLegendOption);
                  break;

                default:
                  break;
              }
            }
          }
        }
      );
    });

    function showTooltip(event: any, content: string): void {
      Tooltip.html(content)
        .style("opacity", 1)
        .style("left", event.pageX + "px")
        .style("top", event.pageY + "px");
    }

    function leaveTooltip(): void {
      Tooltip.style("opacity", 0);
    }

    function clickTab(event: any): void {
      for (let item of tabs_switch.value.children) {
        item.className = "false";
      }
      event.path[0].className = "true";
    }

    const colors =
      props.type == 2
        ? ["#8BEAFF", "#00C7F2"]
        : ["#00C7F2", "#7A6FFF", "#FBB42B", "#2BC679", "#666E7A"];

    const legendOption = {
      color: colors,
      legend: {
        orient: "vertical",
        left: "0",
        top: "center",
        icon: "circle",
        textStyle: {
          color: "#383C41",
        },
      },
      tooltip: {
        trigger: "item",
        backgroundColor: "#383C41",
        borderWidth: 0,
        textStyle: {
          color: "#FCFDFE",
        },
        formatter: (params: any, ticket: any, callback: any) => {
          return params.data.name;
        },
      },
      minShowLabelAngle: 10,
      series: [
        {
          type: "pie",
          radius: "80%",
          avoidLabelOverlap: false,
          left: 100,
          label: {
            show: true,
            position: "inside",
            formatter: "{c} %",
          },
          labelLine: {
            show: false,
          },
          data: legendData,
        },
      ],
    };

    const legendDarkOption = {
      color: colors,
      legend: {
        orient: "vertical",
        left: "0",
        top: "center",
        icon: "circle",
        textStyle: {
          color: "#EEF0F4",
        },
      },
      tooltip: {
        trigger: "item",
        backgroundColor: "#383C41",
        borderWidth: 0,
        textStyle: {
          color: "#FCFDFE",
        },
        formatter: (params: any, ticket: any, callback: any) => {
          return params.data.name;
        },
      },
      minShowLabelAngle: 10,
      series: [
        {
          type: "pie",
          radius: "80%",
          avoidLabelOverlap: false,
          left: 100,
          label: {
            show: true,
            position: "inside",
            formatter: "{c} %",
          },
          labelLine: {
            show: false,
          },
          data: legendData,
        },
      ],
    };

    const noLegendOption = {
      color: colors,
      tooltip: {
        trigger: "item",
        backgroundColor: "#383C41",
        borderWidth: 0,
        textStyle: {
          color: "#FCFDFE",
        },
        formatter: (params: any, ticket: any, callback: any) => {
          return params.data.name;
        },
      },
      minShowLabelAngle: 10,
      series: [
        {
          type: "pie",
          radius: "80%",
          avoidLabelOverlap: false,
          label: {
            show: true,
            position: "inside",
            formatter: "{c} %",
          },
          labelLine: {
            show: false,
          },
          data: legendData,
        },
      ],
    };

    return {
      tabs_switch,
      clickTab,
      showTooltip,
      leaveTooltip,
      chartDOM,
      main,
      only,
    };
  },
});
